/* Base styles for all screen sizes */

.hobbies-container {
  margin-top: 50px;
  text-align: center;
}

.hobbies-container h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 30px;
}

.hobby {
  display: inline-block;
  position: relative;
  margin: 10px;
  width: 200px;
  height: 200px;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.hobby img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease;
}

.hobby:hover img {
  transform: scale(1.2);
}

.hobby-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 10px;
  box-sizing: border-box;
  transform: translateY(50px);
  transition: all 0.5s ease;
}

.hobby:hover .hobby-info {
  transform: translateY(0);
}

/* Media queries for smaller screens */

@media (max-width: 767px) {
  .hobbies-container {
    margin-top: 30px;
  }
  
  .hobbies-container h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .hobby {
    width: 150px;
    height: 150px;
    margin: 5px;
  }
  
  .hobby-info {
    height: 30px;
    font-size: 14px;
    padding: 5px;
  }
}
